import * as React from "react"

export default function SFLogoL(props) {
    return (
        <svg
            xmlns="http://www.w3.org/2000/svg"
            xmlnsXlink="http://www.w3.org/1999/xlink"
            style={{
                isolation: "isolate",
            }}
            viewBox="0 0 140 80"
            width={140}
            height={80}
            {...props}
        >
            <defs>
                <clipPath id="a">
                    <path d="M0 0h140v80H0z" />
                </clipPath>
            </defs>
            <g clipPath="url(#a)">
                <defs>
                    <mask id="c">
                        <path
                            d="M0 0h140v80H0V0Z"
                            style={{
                                stroke: "#000",
                                fill: "#fff",
                                strokeMiterlimit: 10,
                            }}
                        />
                    </mask>
                    <mask id="b">
                        <path
                            d="M0 0h140v80H0V0Z"
                            style={{
                                stroke: "#000",
                                fill: "#fff",
                                strokeMiterlimit: 10,
                            }}
                        />
                    </mask>
                </defs>
                <g mask="url(#c)">
                    <use xlinkHref="#d" transform="scale(.073 .074)" />
                </g>
            </g>
            <defs>
                <image
                    width={1920}
                    height={1080}
                    xlinkHref="data:application/octet-stream;base64,"
                    id="d"
                />
            </defs>
        </svg>
    )

}