import React from 'react';

//Css
import './about.css';

export default function About() {

    return (

        <div className="about-section">
            <div class="cuadro_informacion">
                <div class="cabecera_inf_general">
                    <h1>Información General</h1>
                    <div class="inf_general">
                        <div class="info_text">
                            <h2 class="subtitulo">¿Quién puede inscribirse en los programas de formación del SENA?</h2>
                            <p>Puede inscribirse al SENA todo colombiano o extranjero residente interesado en ingresar a un
                                programa
                                de formación del SENA.</p>
                        </div>
                        <div class="info_text">
                            <h2 class="subtitulo">¿Qué tipo de formación ofrece el SENA?</h2>
                            <p> <b>Carreras técnicas: </b> Programas de hasta dos años de duración para cursar en un Centro de
                                formación
                                o a través de Internet. Ofrecen un título normal reconocido por el ICFES (auxiliar, técnico,
                                tecnólogo, especialización tecnológica u operario).</p>
                            <p><b>Cursos Cortos:</b> Programas de formación complementaria de corta duración</p>
                            <p><b>Formación virtual: </b> de formación complementaria de corta duración para cursar a través de
                                Internet</p>
                            <p> <b>Formación para empresas: </b> Programas a la medida para las empresas. Para acceder a estos
                                programas debe conocer el código suministrado por la empresa para inscribirse</p>
                        </div>
                        <div class="info_text">
                            <h2 class="subtitulo">¿Qué costo tiene la inscripción y la formación en el SENA?</h2>
                            <p>Todos los servicios y procesos de formación que ofrece el SENA son totalmente gratuitos y sin
                                ningún tipo de intermediario.</p>
                        </div>
                        <div class="info_text">
                            <h2 class="subtitulo">¿En qué horarios se ofrecen los programas de formación?</h2>
                            <p>Pensando en la disponibilidad de todas las personas el SENA ofrece diferentes alternativas de
                                horario para nadie se quede sin acceso a la educación: <br /><br />
                                <ul>
                                    <li><b>Diurna </b> entre las 6:00 y las 18:00 horas. <br /><br /></li >
                                    <li><b>Mañana</b> entre las 6:00 y 13:00 horas. <br /><br /></li>
                                    <li><b>Tarde</b> entre las 12:00 y las 18:00 horas <br /><br /></li>
                                    <li><b>Mixta</b> combinación Nocturna entre las 18:00 y las 22:00 horas <br /><br /></li >
                                    <li><b>Madrugada</b> entre las 22:00 y las 6:00 horas <br /><br /></li >
                                    <li><b>Sábados</b> y <b>domingos</b> entre las 6:00 y las 18:00 horas Virtual La dedicación depende
                                        del usuario <br /><br /></li >
                                </ul>
                            </p >
                        </div>
                        <div class="info_text">
                            <h2 class="subtitulo">¿Cómo consultar el número de ofertas disponibles en su ciudad?</h2>
                            <p>Para consultar la oferta disponible en una ciudad debe ingresar en el portal del SENA
                                www.sena.edu.co donde encontrará un enlace al sitio web de inscripciones
                                ofertaeducativa.sena.edu.co. Tenga en cuenta que podrá consultar la oferta educativa haciendo
                                clic en las pestañas "Inicio" o "Buscar oferta". Seleccione el tipo de formación de su interés
                                (carreras técnicas, cursos cortos, formación virtual o formación para empresas). En el campo
                                ¿Qué quiere estudiar? ingrese una o más palabras claves del programa de su interés. En el campo
                                ¿En qué ciudad prefiere estudiar? Digite el nombre de la ciudad donde desea realizar su
                                formación. El sistema le mostrará en la ciudad seleccionada el número de ofertas disponibles.
                                Hacer clic en el botón "Buscar". El sistema mostrará los programas existentes de acuerdo al
                                filtro de consulta establecido.</p>
                        </div>
                        <div class="info_text">
                            <h2 class="subtitulo">¿Qué requisitos se necesitan para inscribirse?</h2>
                            <p>Cada programa establece los requisitos mínimos que deben reunir los Aspirantes para el ingreso y
                                pueden ser vistos al consultar cada uno de los programas ofertados, allí encontrará los
                                requisitos académicos, jornada, duración, horario, modalidad, lugar de realización de la
                                formación, el cronograma del proceso de ingreso, título que se otorga al finalizar, los módulos
                                que los componen. El Aspirante debe garantizar que sus datos de contacto como tipo y número de
                                documento y correo electrónico son los correctos ya que por este medio el SENA le informará como
                                va en su proceso de selección. Si es un Aspirante a un nivel tecnólogo sin excepción deberá
                                tener a mano previamente el registro SNP ICFES. Si la persona no tiene el desprendible de
                                citación puede realizar la consulta de su código SNP a través de la página web
                                www.icfesinteractivo.gov.co.</p>
                        </div>
                        <div class="info_text">
                            <h2 class="subtitulo">Soy técnico del SENA, ¿me puedo inscribir en esta convocatoria?</h2>
                            <p>Teniendo en cuenta lo establecido en la resolución 2130 de 2013 los egresados de programas de
                                formación titulada a nivel técnico del SENA que se encuentren ya certificados estarán
                                habilitados para acceder a otro programa de formación profesional en el SENA, del mismo o más
                                alto nivel de formación.</p>
                        </div>
                        <div class="info_text">
                            <h2 class="subtitulo">Recomendaciones generales para las personas que quieren inscribirse:</h2>
                            <p>Revisar la página del SENA: www.sena.edu.co, una vez allí ubicar el recuadro de SOFIA Plus y
                                buscar el programa de formación de su interés. Recuerde hacer uso de los filtros y leer
                                atentamente el programa de su interés y los requisitos mínimos de ingreso, una vez lo
                                identifique, podrá registrarse en caso de no estarlo e inscribirse.</p>
                        </div>
                        <div class="info_text">
                            <h2 class="subtitulo">¿En qué consiste el horario en modalidad combinada?</h2>
                            <p>La modalidad combinada establece criterios para que tenga actividades presenciales con
                                actividades de tipo desescolarizada (actividades fuera de centro) y presencial con actividades
                                de tipo virtual.
                                <br /> <br />
                                El horario mixto establece un horario de actividades que combina dos jornadas: Diurna entre las
                                6:00 y las 18:00 horas. Mañana entre las 6:00 y 13:00 horas. Tarde entre las 12:00 y las 18:00
                                horas. Mixta combinación Nocturna entre las 18:00 y las 22:00 horas. Madrugada entre las 22:00 y
                                las 6:00 horas. Sábados y domingos entre las 6:00 y las 18:00 horas.
                            </p>
                        </div>
                        <div class="info_text">
                            <h2 class="subtitulo">¿Qué son las modalidades de formación SENA?</h2>
                            <p>Son la forma como el SENA llega a la comunidad para ofrecer acciones de formación profesional que
                                correspondan a las características y necesidades específicas de cada población. La Entidad
                                ejecuta la formación, por medio de las siguientes estrategias:
                                <br /><br />
                                <b>Presencial:</b> en las instalaciones del Centro, en las sedes de las instituciones con las
                                que haya
                                convenios administrativos, o en las aulas móviles. <br />
                                <b>Virtual:</b> mediada por la plataforma de capacitación multimedia en línea o fuera de línea y
                                los
                                recursos tecnológicos disponibles vía Internet (correo, sistema de información, Comunidad
                                Educativa SENA, chat, foros y otros servicios) <br />
                                <b>Distancia:</b> Es una forma de enseñanza que combina las dos modalidades anteriores.
                            </p>
                        </div>
                    </div>
                </div>
                <div class="cabecera_inf_matricula">
                    <h1>Información Matricula</h1>
                    <div class="inf_matricula">
                        <div class="info_text">
                            <h2 class="subtitulo">Matricula</h2>
                            <p>El Coordinador de formación profesional o el coordinador responsable del
                                ingreso realizarán la gestión de matrícula de los aspirantes seleccionados que se inscribieron a
                                una ficha de caracterización o que fueron seleccionados de forma excepcional. Primero convocara
                                a los aspirantes para la entrega de requisitos, lo que permitirá verificar la información de
                                registro del aspirante; luego asentará la matrícula y El sistema le creará el registro académico
                                y el correo electrónico para el nuevo aprendiz. <br /><br /> También, si en algunos casos lo
                                amerita,
                                realizará la cancelación del proceso de matrícula para un aspirante, o la selección excepcional
                                de una persona y al final del proceso generar el reporte de estadísticas de todo el ingreso de
                                aspirantes que servirá como informe de análisis de inteligencia.</p>
                        </div>
                        <div class="info_text">
                            <h2 class="subtitulo">Cancelación Matricula</h2>
                            <p>Estos casos se pueden dar por diversas situaciones: <br /><br />Omisión o falsedad en la
                                documentación presentada. <br /><br /> Presentación de un documento de identificación diferente al
                                registrado en el formulario de inscripción.<br /><br /> Faltas disciplinarias. <br /><br />
                                Inasistencias
                                Injustificadas. <br /><br /> Deserción etapa
                                lectiva. <br /><br />Deserción etapa práctica. <br /><br />Sanciones impuestas mediante debido proceso
                                luego de
                                un plan de mejoramiento. <br /><br /> En algunos casos los centros de formación cancelan las fichas
                                (grupos de un programa de formación) debido a que no se completan los cupos. En esta situación
                                unifican un grupo con los aprendices disponibles.
                                En este cambio de un grupo a otro, es probable que usted reciba una notificación inicial de
                                cancelación de matrícula y luego vuelva a recibir otra notificación indicándole que de nuevo se
                                encuentra en estado "matriculado". Por esta razón se recomienda hacer revisión permanente de su
                                estado en el aplicativo SOFIA Plus al igual que estar atento a cualquier notificación en el
                                correo electrónico que indico en el momento de su registro.</p>

                        </div>
                    </div>
                </div>
            </div>
        </div>

    )

}